import React, { useEffect, useMemo } from 'react'
import { graphql, navigate } from 'gatsby'
import { useFormikContext } from 'formik';
import { FiArrowLeft } from "react-icons/fi";
import { IconButton, useToken } from '@chakra-ui/react'
import { set } from 'lodash';
import { StaticImage } from 'gatsby-plugin-image';
import AniLink from "gatsby-plugin-transition-link/AniLink"

import Box from '../components/Box'
import Text from '../components/Text'
import Flex from '../components/Flex'
import Button from '../components/Button'
import Container from '../components/Container'
import { Media, responsive } from '../components/ThemeProvider/theme';

import Layout from '../containers/Layout'
import { trackAnswer } from '../utils/trackEvent';

const layout = 'fullWidth'

const picture = {
  1: <StaticImage alt="Q1" src="../image/q1.png" layout={layout} />,
  2: <StaticImage alt="Q2" src="../image/q2.png" layout={layout} />,
  3: <StaticImage alt="Q3" src="../image/q3.png" layout={layout} />,
  4: <StaticImage alt="Q4" src="../image/q4.png" layout={layout} />,
  5: <StaticImage alt="Q5" src="../image/q5.png" layout={layout} />,
  6: <StaticImage alt="Q6" src="../image/q6.png" layout={layout} />,
}

const Question = ({ data, pageContext: { id, isLast, length } }) => {
  // const [teal] = useToken('colors', ['custom.teal'])
  const { values, submitForm, isSubmitting } = useFormikContext()
  const answerNotMatch = useMemo(() => values.answers.length < id - 1, [id, values.answers.length])
  useEffect(() => {
    if (answerNotMatch) {
      navigate(`/q/${values.answers.length + 1}`)
    }
  }, [answerNotMatch])
  if (answerNotMatch) return null
  return (
    <Layout noHeader bg="white" footerProps={{ pt: responsive('1.25em', '3.5em'), pb: '1.25em' }} title={`第${id}題`}>
      <Container pt={responsive('1em', '3.5em')}>
        <Box mx={responsive('auto', 0)} width={responsive('6.375em', '8.625em')}>
          <StaticImage src="../containers/teal_logo.png" layout="fullWidth" />
        </Box>
        <Flex alignItems="flex-end" justifyContent="space-around">
          <Box width="424px" as={Media} greaterThanOrEqual="tablet">
            {picture[id]}
          </Box>
          <Box>
            <Flex
              pb="0.25em"
              alignItems="center"
              justify="space-between"
              borderColor="custom.borderGray"
              borderBottom="1px solid"
            >
              <IconButton
                as={AniLink}
                fade
                w="1.5em"
                h="1.5em"
                mr="0.25em"
                minWidth="auto"
                bg="transparent"
                color="custom.darkBlue"
                icon={<FiArrowLeft size="2em" />}
                to={+id > 1 ? `/q/${id - 1}` : '/'}
                isDisabled={isSubmitting}
              />
              <Text
                fontSize={responsive('1.5em', '1.25em')}
                letterSpacing="0.125em"
                fontFamily="Flama"
                color="lightGray"
              >
                <Text.Inline color="gray">{id}</Text.Inline>/{length}
              </Text>
            </Flex>
            <Text
              mt={responsive('0.5em', '2.375em')}
              fontWeight="500"
              color="custom.darkBlue"
              fontSize={responsive('1.125em', '1.25em')}
            >{data?.questionsJson.question}</Text>
            <Box as={Media} lessThan="tablet" pt="1em">
              {picture[id]}
            </Box>
            <Box>
              {data?.questionsJson.options.map((opt, i) => (
                <Box mt={responsive(i && '1em' , i ? '1.25em' : '3.25rem')} key={i}>
                  <Button
                    as={!isLast && ((p) => <AniLink {...p} fade to={`/q/${+id + 1}`} />)}
                    onClick={() => {
                      set(values, ['answers', +id - 1], i)
                      trackAnswer(id, i)
                      if(isLast) {
                        submitForm()
                      }
                    }}
                    width="100%"
                    isDisabled={isSubmitting}
                  >
                    {opt}
                  </Button>
                </Box>
              ))}
            </Box>
          </Box>
        </Flex>
      </Container>
    </Layout>
  )
}

export default Question

export const query = graphql`
  query QuestionPageQuery($id: String) {
    questionsJson(id: {eq: $id}) {
      question
      options
    }
  }
`


